@value defaultFontColor, listBoxShadow, colorBlueGrey from "../../common/colors.css";
@value titleSize from "../../common/size.css";

.domestic {
  flex-basis: 80%;
  width: 100%;
  height: 100%;
}

.title {
  text-align: center;
  font-size: titleSize;
  color: defaultFontColor;
}

.total,
.region__list {
  display: flex;
  justify-content: center;
}

.region__list {
  padding-bottom: 2em;
}

.selection {
  display: flex;
  justify-content: center;
  margin: 1.5em;
}

.button {
  margin: 0 1em;
  width: 8rem;
  height: 2rem;
  border-radius: 0.5em;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 1.2rem;
}

.checked {
  opacity: 0.5;
}

table {
  border-collapse: collapse;
  border-color: colorWhite;
}

thead {
  background-color: colorBlueGrey;
}

tr,
td {
  text-align: center;
  color: defaultFontColor;
}

td {
  padding: 0.5em 1.2em;
  width: 7.5rem;
}

.small_list {
  display: none;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .total {
    flex-direction: column;
    align-items: center;
  }

  table {
    display: none;
  }

  .small_list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
